function buttonsSettings() {
  $(".buttons-settings").each(function () {
    const e = $(this).find(".buttons-settings__button");
    e.click(function () {
      e.filter(".active").not($(this)).removeClass("active"),
        $(this).addClass("active");
    });
  });
}
function choicesItems() {
  $(".choices-group").each(function () {
    const e = $(this).find(".choices-item");

    e.click(function () {
      const categoryTitleText = $(this)
        .parent()
        .find(".choices-group-title")
        .text();

      const category = $(
        `[data-category="${categoryTitleText}"]`
      );

      const choicesItemText = $(this).find("span").text();

      const checkbox = category
        .find(`span:contains("${choicesItemText}")`)
        .prev();

      checkbox.removeClass("is-checked");
      checkbox.prop("checked", false);
      removeChoice(categoryTitleText, choicesItemText);
    });
  });
}
function choseCategories() {
  let chosenCategory = {};
  $(".categories-checkbox").each(function () {
    const checkbox = $(this).find("input");
    checkbox.click(function () {
      if (checkbox.is(":checked")) {
        chosenCategory.parentName = checkbox
          .closest("[data-category]")
          .data('category');
        chosenCategory.itemName = $(this).next().text();

        if ($(".choices-container").hasClass("is-hidden")) {
          $(".choices-container").removeClass("is-hidden");
        }

        addChoice(chosenCategory.parentName, chosenCategory.itemName);
      } else {
        if (getChoicesChildrenNum() === 0) {
          $(".choices-container").addClass("is-hidden");
        }

        chosenCategory.itemName = $(this).next().text();
        chosenCategory.parentName = $(this)
          .closest("[data-category]")
          .data('category');

        removeChoice(chosenCategory.parentName, chosenCategory.itemName);
      }
    });
  });
}
function addChoice(groupTitleText, groupItemText) {
  let isInNthGroups = $(".choices").children(".choices-group").length;
  $(".choices-group").each(function () {
    if ($(this).find(".choices-group-title").text() === groupTitleText) {
      $(this).append(`
	  	<div class="choices-item btn">
		  <span>${groupItemText}</span><span class="icon-[custom--cross]"></span>
  	    </div>`);
      // return false;
    } else {
      isInNthGroups--;
    }
  });

  if (isInNthGroups == 0) {
    const choiceGroup = `
    	<div class="choices-group">
    	  <p class="choices-group-title">${groupTitleText}</p>
    	  <div class="choices-item btn">
  		    <span>${groupItemText}</span><span class="icon-[custom--cross]"></span>
    	  </div>
  	    </div>`;
    $(".choices").append(choiceGroup);
  }

  choicesItems();
}
function removeChoice(groupTitleText, groupItemText) {
  $(".choices-group").each(function () {
    let groupLength = $(this).children(".choices-item").length;
    if ($(this).find(".choices-group-title").text() === groupTitleText) {
      $(this)
        .find(".choices-item")
        .each(function () {
          if ($(this).find("span").text() === groupItemText) {
            $(this).fadeOut("normal", function () {
              groupLength--;
              if (groupLength === 0) {
                $(this)
                  .parent()
                  .fadeOut("normal", function () {
                    $(this).remove();

                    if (getChoicesChildrenNum() === 0) {
                      $(".choices-container").addClass("is-hidden");
                    }
                  });
              }

              $(this).remove();
            });
          }
        });
    }
  });
  choicesItems();
}
function getChoicesChildrenNum() {
  return $(".choices").children().length;
}
function moveChoices() {
  if (window.matchMedia("screen and (max-width: 64em)").matches) {
    $(".filter__content").prepend($(".choices-container"));
  } else {
    $("#filters").prepend($(".choices-container"));
  }
}
function categoryLink() {
  $(".link-category-list").each(function () {
    const e = $(this).find(".link-category");
    e.click(function () {
      e
        .filter(".active")
        .not($(this))
        .removeClass("active")
        .next()
        .animate({ opacity: "toggle", height: "toggle" }, 200),
        $(this).toggleClass("active"),
        $(this).next().animate({ opacity: "toggle", height: "toggle" }, 200);
    });
  });
}
