function priceSlider() {
  document.querySelectorAll(".range").forEach((e) => {
    const a = e.querySelector(".range__selected"),
      i = e.querySelectorAll(".range__input input"),
      o = e.querySelectorAll(".range__price input");
      i.forEach((e) => {
        e.addEventListener("input", (e) => {
          var t = parseInt(i[0].value),
            n = parseInt(i[1].value);
          var min = parseInt(e.target.min), max = parseInt(e.target.max), step = parseInt(e.target.step);
          var bar = max - min;
          n - t < step
            ? (i[0].value = n - step) && (i[1].value = t + step)
            : ((o[0].value = t),
              (o[1].value = n),
              (a.style.left = (((t - min) / bar) * 100) + "%"),
              (a.style.right = 100 - ((n - min) / bar) * 100 + "%"));
        });
      });
      o.forEach((e) => {
        var t = o[0].value,
          n = o[1].value;
        var min = parseInt(e.min), max = parseInt(e.max), step = parseInt(e.step), value = parseInt(e.value);
        var bar = max - min;
        (i[0].value = t),
        (a.style.left = (((t - min) / bar) * 100) + "%"),
        (a.style.right = 100 - ((n - min) / bar) * 100 + "%");
          e.addEventListener("change", (e) => {
            var t = parseInt(o[0].value),
              n = parseInt(o[1].value);
            var min = parseInt(e.target.min), max = parseInt(e.target.max), step = parseInt(e.target.step), value = parseInt(e.target.value)
            var bar = max - min;
            value < min ? e.target.value = min : value > max ? e.target.value = max : undefined;
            value < t ? e.target.value = t + step : value > n ? e.target.value = n - step  : undefined;
            var t = parseInt(o[0].value),
              n = parseInt(o[1].value);
            n - t >= step &&
              n <= max && ((a.style.left = (((t - min) / bar) * 100) + "%"),
              (a.style.right = 100 - ((n - min) / bar) * 100 + "%")) && ((i[0].value = t)) && ((i[1].value = n));
          }
        );
      });
  });
}
function dropdowns() {
  $(".dropdown-products__trigger").each(function () {
    const e = $(this);
    e.click(function () {
      const content = e.next();
      if (content.hasClass("is-open")) {
        content.removeClass("is-open").fadeOut();
      } else {
        content.addClass("is-open").fadeIn();
      }
    });
  });
}
$(document).ready(function () {
  moveChoices(),
    buttonsSettings(),
    choicesItems(),
    choseCategories(),
    categoryLink(),
    // modernDropdown(),
    // modernPopUp(),
    priceSlider(),
    dropdowns(),
    $(window).resize(
      $.debounce(250, function () {
        moveChoices();
      })
    );
});
